<template>
  <ion-page>
    <ion-header mode="ios">
      <ion-toolbar>
        <ion-title>Pembahasan TO Kedinasan</ion-title>
        <ion-back-button slot="start" default-href="/" />
      </ion-toolbar>
    </ion-header>

    <ion-content :fullscreen="true" class="exam-page">
      <ion-content class="ion-padding">
        <div v-if="currentQuestionIndex < questions.length">
          <!-- Menampilkan Soal -->
          <ion-card>
            <ion-card-header>
              <ion-card-title>
                <div v-html="questions[currentQuestionIndex].question"></div>
              </ion-card-title>
            </ion-card-header>
            <ion-card-content>
              <div v-if="questions[currentQuestionIndex].imageUrl">
                <ion-img :src="questions[currentQuestionIndex].imageUrl"></ion-img>
              </div>
              <div v-if="questions[currentQuestionIndex].videoUrl">
                <video :src="questions[currentQuestionIndex].videoUrl" controls></video>
              </div>
            </ion-card-content>
          </ion-card>

          <!-- Menampilkan Semua Pilihan Jawaban -->
          <div class="options">
            <ion-card
              v-for="(option, key) in questions[currentQuestionIndex].options"
              :key="key"
              :class="{ 
                'correct-answer': key === questions[currentQuestionIndex].correctAnswer,
                'user-answer': key === userAnswers[currentQuestionIndex] 
              }"
            >
              <ion-card-content>{{ option }}</ion-card-content>
            </ion-card>
          </div>

          <!-- Menampilkan Jawaban Benar, Jawaban User, dan Pembahasan -->
          <div class="explanation">
            <p><strong>Jawaban yang benar:</strong> {{ questions[currentQuestionIndex].options[questions[currentQuestionIndex].correctAnswer] }}</p>
            <p><strong>Jawaban Anda:</strong> {{ questions[currentQuestionIndex].options[userAnswers[currentQuestionIndex]] }}</p>
            <p><strong>Pembahasan:</strong></p>
            <div v-html="questions[currentQuestionIndex].explanation"></div>
          </div>

          <!-- Tombol Next untuk pindah ke soal berikutnya -->
          <ion-button style="color: white" expand="block" @click="nextQuestion">
            {{ currentQuestionIndex === questions.length - 1 ? "Selesai" : "Soal Berikutnya" }}
          </ion-button>
        </div>
      </ion-content>
    </ion-content>
  </ion-page>
</template>

<script setup>
import {
  IonPage,
  IonHeader,
  IonToolbar,
  IonTitle,
  IonContent,
  IonButton,
  IonBackButton,
  IonCard,
  IonCardHeader,
  IonCardTitle,
  IonCardContent,
  IonImg,
} from "@ionic/vue";
import { ref, onMounted } from "vue";
import { getFirestore, doc, getDoc, collection, getDocs } from "firebase/firestore";
import { getAuth } from "firebase/auth";
import { useRoute } from "vue-router";
import { firebaseApp } from "../firebase.js";

const db = getFirestore(firebaseApp);
const auth = getAuth(firebaseApp);
const route = useRoute();
const jadwalId = ref(route.params.jadwalId);
const questions = ref([]);
const userAnswers = ref([]);
const currentQuestionIndex = ref(0);

const fetchQuestions = async () => {
  try {
    const jadwalDoc = await getDoc(doc(db, "jadwalkedinasan", jadwalId.value));
    if (jadwalDoc.exists()) {
      const jadwalData = jadwalDoc.data();
      const questionsSnapshot = await getDocs(collection(db, "soalkedinasan"));
      questions.value = questionsSnapshot.docs
        .map((doc) => ({
          id: doc.id,
          ...doc.data(),
        }))
        .filter((question) => jadwalData.soal && jadwalData.soal.includes(question.id));

      // Ambil jawaban pengguna dari `examResults/answers`
      const user = auth.currentUser;
      if (user) {
        const answersDoc = await getDoc(doc(db, `examResults/${user.uid}/answers`, jadwalId.value));
        if (answersDoc.exists()) {
          userAnswers.value = answersDoc.data().answers || [];
        } else {
          console.error("Data jawaban tidak ditemukan.");
        }
      }
    } else {
      console.error("Dokumen jadwal tidak ditemukan di koleksi 'jadwalkedinasan'.");
    }
  } catch (error) {
    console.error("Error fetching documents:", error);
  }
};

const nextQuestion = () => {
  if (currentQuestionIndex.value < questions.value.length - 1) {
    currentQuestionIndex.value++;
  }
};

onMounted(() => {
  fetchQuestions();
});
</script>

<style>
.exam-page {
  position: relative;
}
.options {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
}
.options ion-card {
  flex: 1 1 calc(50% - 10px);
  text-align: center;
}
.correct-answer {
  border: 2px solid green;
  background-color: #e6ffe6;
}
.user-answer {
  border: 2px solid blue;
  background-color: #e6f7ff;
}
.explanation {
  margin-top: 20px;
  padding: 15px;
  background-color: #f9f9f9;
  border-radius: 8px;
}
</style>
